import React, {useCallback, useEffect, useState} from 'react';
import Dashboard from "../Dashboard";
import Paper from "@material-ui/core/Paper";
import {makeStyles} from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import Pagination from "material-ui-flat-pagination";
import axios from "axios";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import TextField from "@material-ui/core/TextField";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import Fab from "@material-ui/core/Fab";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import AddIcon from '@material-ui/icons/Add';
import {history} from "react-router-guard";
const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
    },
    toolbar: {
        paddingRight: 24, // keep right padding when drawer closed
    },
    toolbarIcon: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: '0 8px',
        ...theme.mixins.toolbar,
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: 36,
    },
    menuButtonHidden: {
        display: 'none',
    },
    title: {
        flexGrow: 1,
    },
    drawerPaper: {
        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerPaperClose: {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        width: theme.spacing(7),
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9),
        },
    },
    appBarSpacer: theme.mixins.toolbar,
    content: {
        flexGrow: 1,
        height: '100vh',
        overflow: 'auto',
    },
    container: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(4),
    },
    paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    fixedHeight: {
        height: 240,
    },
    table: {
        minWidth: 650,
    },
    paperr: {
        width: '100%',
        overflowX: 'auto',
    },
    addIcon: {
        float: 'right',
        marginTop: '16px'
    }
}));


export default function Entries() {
    const classes = useStyles();
    const [offset, setOffset] = useState(1);
    const [records, setRecords] = useState([]);
    const [total, setTotal] = useState(0);
    const [perPage, setPerPage] = useState(10);
    // const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

    const updatePage = useCallback((offset, perPage) => {
        axios.get(process.env.REACT_APP_API_BASE_URL + '/api/dictionary/entries', {
            params: {
                page: parseInt(offset / perPage) + 1
            },
            headers: {
                Authorization: 'Bearer ' + localStorage.getItem('access_token')
            }
        }).then((resp) => {
            console.log(resp);
            setRecords(resp.data.data);
            setTotal(resp.data.total);
            setPerPage(resp.data.per_page);
        }).catch(err => {
            if (err.response.status === 403) {
                console.log('catcheddddddddd');
                localStorage.removeItem('access_token');
                localStorage.removeItem('expires_in');
                history.push('/login');
            }
        });
        console.log('called', offset);
    }, [setRecords, setTotal, setPerPage]);

    useEffect(() => {
        updatePage(offset, perPage);
    }, [offset, perPage, updatePage]);



    const [editingRecord, setEditingRecord] = React.useState({});


    const [open, setOpen] = React.useState(false);

    const handleClickOpen = (row) => {
        console.log('rowwwwwwww', row);
        let editingRecord = Object.assign({}, row);
        if (Object.keys(editingRecord).length === 0) {
            console.log('hhhhhhhhhhhhh');
            editingRecord = {
                phrase: '',
                example: '',
                meaning: '',
                meaning_fa: '',
            };
        }
        setEditingRecord(editingRecord);
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };




    const [open2, setOpen2] = React.useState(false);
    const [deletingRecord, setDeletingRecord] = React.useState({});



    const handleClickOpen2 = (row) => {
        setDeletingRecord(row);
        setOpen2(true);
    };

    const handleClose2 = () => {
        setOpen2(false);
    };

    const editRecord = (field, value) => {
        const target = Object.assign({}, editingRecord);
        target[field] = value;
        setEditingRecord(target);
    };

    const deletePhrase = () => {
        axios.delete(process.env.REACT_APP_API_BASE_URL + '/api/dictionary/entries/' + deletingRecord.id)
            .then(resp => {

                console.log(resp);
                if (resp.data.done) {
                    setOpen2(false);
                    updatePage(offset, perPage);
                }
            })
            .catch((err) => {
            });
    };

    const updateRecord = () => {
        console.log(editingRecord);
        const url = process.env.REACT_APP_API_BASE_URL + '/api/dictionary/entries' + (editingRecord.id ? '/' + editingRecord.id : '');

        axios.post(url, editingRecord)
            .then(resp => {

                console.log(resp);
                if (resp.data.done) {
                    setOpen(false);
                    updatePage(offset, perPage);
                }
            })
            .catch((err) => {
            });
    };
    return (
        <Dashboard>

            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Edit entry</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Edit the entry
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="phrase"
                        label="Phrase"
                        type="text"
                        fullWidth
                        value={editingRecord.phrase}
                        onChange={(event) => {editRecord('phrase', event.target.value)}}
                    />
                    <TextField
                        margin="dense"
                        id="phrase"
                        label="Example"
                        type="text"
                        fullWidth
                        multiline
                        value={editingRecord.example}
                        onChange={(event) => {editRecord('example', event.target.value)}}
                    />
                    <TextField
                        margin="dense"
                        id="meaning"
                        label="Meaning"
                        type="text"
                        fullWidth
                        value={editingRecord.meaning}
                        onChange={(event) => {editRecord('meaning', event.target.value)}}
                    />
                    <TextField
                        margin="dense"
                        id="meaning"
                        label="Translation"
                        type="text"
                        fullWidth
                        value={editingRecord.meaning_fa}
                        onChange={(event) => {editRecord('meaning_fa', event.target.value)}}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Cancel
                    </Button>
                    <Button onClick={() => {updateRecord()}} color="primary">
                        Change
                    </Button>
                </DialogActions>
            </Dialog>










            <Dialog
                open={open2}
                onClose={handleClose2}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">Are you sure you want to delete?</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Are you sure you want to delete the phrase "{deletingRecord.phrase}"
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose2} color="primary">
                        No
                    </Button>
                    <Button onClick={deletePhrase} color="primary" autoFocus>
                        Yes
                    </Button>
                </DialogActions>
            </Dialog>





            <Paper className={classes.paperr}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Phrase</TableCell>
                            <TableCell>Example</TableCell>
                            <TableCell>Meaning</TableCell>
                            <TableCell>Translation</TableCell>
                            <TableCell>Actions</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {records.map(row => (
                            <TableRow key={row.id}>
                                <TableCell>{row.phrase}</TableCell>
                                <TableCell>{row.example}</TableCell>
                                <TableCell>{row.meaning}</TableCell>
                                <TableCell>{row.meaning_fa}</TableCell>
                                <TableCell>
                                    <Fab color="primary" size="small" aria-label="edit" onClick={() => {handleClickOpen(row)}}>
                                        <EditIcon />
                                    </Fab>
                                    <Fab color="secondary" size="small" aria-label="remove" onClick={() => {handleClickOpen2(row)}}>
                                        <DeleteIcon />
                                    </Fab>

                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </Paper>
            <Fab color="primary" aria-label="add" className={classes.addIcon} size="large" onClick={() => {handleClickOpen({})}}>
                <AddIcon />
            </Fab>

            <br/>
            <Pagination
                limit={perPage}
                offset={offset}
                total={total}
                onClick={(e, offset) => setOffset(offset)}
            />

        </Dashboard>
    );
}